import Vue from 'vue'
import Router, { Route } from 'vue-router'
import store from './store'
import { userAccessAttributes } from './api'

const Login = () => import('./pages/Auth/Login.vue')
const ResetPassword = () => import('./pages/Auth/ResetPassword.vue')
const TellUs = () => import('./pages/Profile/TellUs.vue')
const NewPassword = () => import('./pages/Profile/NewPassword.vue')
const ProfileSettings = () => import('./pages/ProfileSettings.vue')
const Dashboard = () => import('./pages/Dashboard.vue')
const FemaleProfile = () => import('./pages/FemaleProfile.vue')
const NotFound = () => import('./pages/NotFound.vue')

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        { path: '/', component: Login },
        { path: '/login', component: Login },
        { path: '/reset-password', component: ResetPassword },
        { path: '/tell-us', component: TellUs },
        { path: '/new-password', component: NewPassword },
        { path: '/profile-settings', component: ProfileSettings },
        { path: '/dashboard', component: Dashboard },
        { path: '/female-profile', component: FemaleProfile },
        { path: '/not-found', component: NotFound },
    ],
})

const beforeEach = (to: Route, from: Route, next: Function) => {
    const accessToken = localStorage.getItem('access-token')
    const userId = localStorage.getItem('user-id')
    if (accessToken && userId) {
        userAccessAttributes(userId)
            .then(({ data }: any) => {
                if (data && data.userAccessAttributes) {
                    store.commit('SET_FEATURES', data.userAccessAttributes)
                }
                next()
            })
    } else {
        next()
    }
}

router.beforeEach((to, from, next) => {
    setTimeout(beforeEach, 0, to, from, next)
})

export default router
