import { createProvider } from '../vue-apollo'

const apollo = createProvider()

export const mutate = (mutation, params) => apollo.defaultClient.mutate({
    mutation,
    // Parameters
    variables: {
        input: {
            ...params,
        },
    },

}).catch((error) => {
    throw error
})

export const query = (query, params) => apollo.defaultClient.query({
    query,
    // Parameters
    variables: {
        ...params,
    },
}).catch((error) => {
    throw error
})
