import gql from 'graphql-tag'
import { query } from '../api'

/**
 * @param id - User Identifier
 */
export const userAccessAttributes = (id: string) => query(gql`query ($id: ID!) {
    userAccessAttributes(id: $id) {
        version
        userId
        attributes {
            name
            value
        }
    }
}`, { id })
