import { MutationTree } from 'vuex'
import { Feature, FeaturesState } from '../types'

const state: FeaturesState = {
    features: {},
}

const mutations: MutationTree<FeaturesState> = {
    SET_FEATURES(state, features: Feature) {
        state.features = features
    },
}

const stateFeatures = {
    state,
    mutations,
}

export default stateFeatures
